import React from 'react';

import classnames from 'classnames';
import {
    InspectorControls,
    useBlockProps,
    __experimentalPanelColorGradientSettings as PanelColorGradientSettings
} from '@wordpress/block-editor';
import {PanelBody, TextControl, RadioControl, Disabled, SelectControl} from '@wordpress/components';
import {__} from '@wordpress/i18n';
import {SearchField} from '../../../../base/components';
import BorderSizeControl from './border-size-control';
import {YWCAS_DEFAULT_QUERY} from "../../../../config";
import {useEditorContext} from "../../../../base/providers/editor-context";

const Edit = ({attributes, className, setAttributes, align}) => {
    const {
        placeholder,
        submitContentColor,
        submitContentHoverColor,
        submitBgColor,
        submitBgHoverColor,
        submitBorderColor,
        submitBorderHoverColor,
        inputBorderSize,
        inputBorderRadius,
        placeholderTextColor,
        inputTextColor,
        inputBgColor,
        inputBgFocusColor,
        inputBorderColor,
        inputBorderFocusColor,
        iconType,
        buttonLabel,
        buttonBorderRadius,
        submitStyle,
    } = attributes;

    const blockProps = useBlockProps();
    const {size} = useEditorContext();
    const classes = classnames(
        'yith-input-block',
        align ? 'align' + align : '',
        className,
    );

    const submitSelectColorsLabels = [
        {key: 'icon', value: __('Icon', 'yith-woocommerce-ajax-search')},
        {key: 'text', value: __('Text', 'yith-woocommerce-ajax-search')},
        {key: 'iconText', value: __('Text and Icon', 'yith-woocommerce-ajax-search')},
        {key: 'iconHover', value: __('Icon Hover', 'yith-woocommerce-ajax-search')},
        {key: 'textHover', value: __('Text Hover', 'yith-woocommerce-ajax-search')},
        {key: 'iconTextHover', value: __('Text and Icon Hover', 'yith-woocommerce-ajax-search')},
    ];
    const labelSubmitContent = submitSelectColorsLabels.find(item => item.key === submitStyle);
    const labelSubmitContentHover = submitSelectColorsLabels.find(item => item.key === submitStyle + 'Hover');
    const handleBorderControlChange = (value, attribute) => {
        let newBorderSize = value;
        if (typeof value === 'string') {
            newBorderSize = {
                topLeft: value,
                topRight: value,
                bottomLeft: value,
                bottomRight: value,
            };
        }
        const newAttributes = {...attributes};
        newAttributes[attribute] = newBorderSize;
        setAttributes(newAttributes);
    };

    let submitSettings = [
        {
            label: labelSubmitContent.value,
            onColorChange: (color) => setAttributes({submitContentColor: color}),
            colorValue: submitContentColor,
        },
        {
            label: labelSubmitContentHover.value,
            onColorChange: (color) => setAttributes({submitContentHoverColor: color}),
            colorValue: submitContentHoverColor,
        },
        {
            label: __('Background', 'yith-woocommerce-ajax-search'),
            onColorChange: (color) => setAttributes({submitBgColor: color}),
            colorValue: submitBgColor,
        },
        {
            label: __('Background Hover', 'yith-woocommerce-ajax-search'),
            onColorChange: (color) => setAttributes({submitBgHoverColor: color}),
            colorValue: submitBgHoverColor,
        },
        {
            label: __('Border', 'yith-woocommerce-ajax-search'),
            onColorChange: (color) => setAttributes({submitBorderColor: color}),
            colorValue: submitBorderColor,
        },
        {
            label: __('Border Hover', 'yith-woocommerce-ajax-search'),
            onColorChange: (color) => setAttributes({submitBorderHoverColor: color}),
            colorValue: submitBorderHoverColor,
        }
    ];


    return (
        <>
            <InspectorControls key="inspector">
                <PanelBody
                    title={__('Search input settings', 'yith-woocommerce-ajax-search')}
                    initialOpen
                >
                    <TextControl
                        label={__('Placeholder', 'yith-woocommerce-ajax-search')}
                        className="ywcas-block-input__field input-control"
                        value={placeholder}
                        placeholder={__(
                            'Enter search placeholder text',
                            'yith-woocommerce-ajax-search',
                        )}
                        onChange={(value) =>
                            setAttributes({placeholder: value})
                        }
                    />
                    <BorderSizeControl
                        type="border"
                        label={__('Border Size', 'yith-woocommerce-ajax-search')}
                        values={inputBorderSize}
                        onChange={(value) => handleBorderControlChange(value, 'inputBorderSize')}
                    />
                    <BorderSizeControl
                        type="radius"
                        label={__('Border radius', 'yith-woocommerce-ajax-search')}
                        values={inputBorderRadius}
                        onChange={(value) => handleBorderControlChange(value, 'inputBorderRadius')}
                    />

                    <PanelColorGradientSettings
                        title={__('Color Settings', 'yith-woocommerce-subscription')}
                        initialOpen={false}
                        settings={[
                            {
                                label: __('Placeholder', 'yith-woocommerce-ajax-search'),
                                onColorChange: (color) => setAttributes({placeholderTextColor: color}),
                                colorValue: placeholderTextColor,
                            },
                            {
                                label: __('Text', 'yith-woocommerce-ajax-search'),
                                onColorChange: (color) => setAttributes({inputTextColor: color}),
                                colorValue: inputTextColor,
                            },
                            {
                                label: __('Background', 'yith-woocommerce-ajax-search'),
                                onColorChange: (color) => setAttributes({inputBgColor: color}),
                                colorValue: inputBgColor,
                            },
                            {
                                label: __('Background focus', 'yith-woocommerce-ajax-search'),
                                onColorChange: (color) => setAttributes({inputBgFocusColor: color}),
                                colorValue: inputBgFocusColor,
                            },
                            {
                                label: __('Border', 'yith-woocommerce-ajax-search'),
                                onColorChange: (color) => setAttributes({inputBorderColor: color}),
                                colorValue: inputBorderColor,
                            },
                            {
                                label: __('Border focus', 'yith-woocommerce-ajax-search'),
                                onColorChange: (color) => setAttributes({inputBorderFocusColor: color}),
                                colorValue: inputBorderFocusColor,
                            },
                        ]}
                    />

                    <SelectControl
                        label={__('Submit search style', 'yith-woocommerce-ajax-search')}
                        value={submitStyle}
                        options={[
                            {label: __('Icon', 'yith-woocommerce-ajax-search'), value: 'icon'},
                            {label: __('Text', 'yith-woocommerce-ajax-search'), value: 'text'},
                            {label: __('Icon + Text', 'yith-woocommerce-ajax-search'), value: 'iconText'},
                        ]}
                        onChange={(value) => setAttributes({submitStyle: value})}
                        __nextHasNoMarginBottom
                    />
                    {submitStyle === 'icon' && <RadioControl
                        label={__('Search icon', 'yith-woocommerce-ajax-search')}
                        selected={iconType}
                        onChange={(value) => setAttributes({iconType: value})}
                        options={[
                            {label: __('Icon on the right', 'yith-woocommerce-ajax-search'), value: 'icon-right'},
                            {label: __('Icon on the left', 'yith-woocommerce-ajax-search'), value: 'icon-left'},
                        ]}
                    />}

                    {submitStyle !== 'icon' && (
                        <>
                            <TextControl
                                label={__('Submit Button Label', 'yith-woocommerce-ajax-search')}
                                value={buttonLabel}
                                placeholder={__(
                                    'Enter button label',
                                    'yith-woocommerce-ajax-search',
                                )}
                                onChange={(value) =>
                                    setAttributes({buttonLabel: value})
                                }
                            />
                            <BorderSizeControl
                                type="radius"
                                label={__('Submit Border Radius', 'yith-woocommerce-ajax-search')}
                                values={buttonBorderRadius}
                                onChange={(value) => handleBorderControlChange(value, 'buttonBorderRadius')}
                            />
                        </>
                    )}

                    <PanelColorGradientSettings
                        title={__('Submit Color Settings', 'yith-woocommerce-subscription')}
                        initialOpen={false}
                        settings={submitSettings}
                    />
                </PanelBody>
            </InspectorControls>

            <div className={classes} {...blockProps}>
                <div className="ywcas-block-input">
                    <Disabled>
                        <SearchField queryString={YWCAS_DEFAULT_QUERY} {...attributes} size={size}/>
                    </Disabled>
                </div>
            </div>

        </>
    );
};

export default Edit;