import React from 'react';
import {PanelBody, TextControl, RangeControl, Disabled} from '@wordpress/components';
import {InspectorControls, useBlockProps} from '@wordpress/block-editor';
import {__} from '@wordpress/i18n';

import classnames from 'classnames';
import {HistorySearches} from "../../../../base/components";
import {history} from "../../../../config";

const Edit = ({attributes, setAttributes, className, align}) => {

    const classes = classnames(
        'ywcas-last-searches-wrapper',
        align ? align : '',
        className,
    );

    const blockProps = useBlockProps();
    const {historyHeading, maxHistoryResults} = attributes;
    const historyItems = history.slice(0, maxHistoryResults);

    return (
        <div {...blockProps}>
            <InspectorControls key="inspector">
                <PanelBody
                    title={__('Settings', 'yith-woocommerce-ajax-search')}
                    initialOpen
                >
                    <TextControl
                        label={__('Label', 'yith-woocommerce-ajax-search')}
                        className="wc-block-product-search__field input-control"
                        value={historyHeading}
                        placeholder={__(
                            'Enter the label for the search history',
                            'yith-woocommerce-ajax-search',
                        )}
                        onChange={(value) =>
                            setAttributes({historyHeading: value})
                        }
                    />

                    <RangeControl
                        help={__('Please, select the maximum number of the latest searches results', 'yith-woocommerce-ajax-search')}
                        initialPosition={maxHistoryResults}
                        label={__('Max latest search items', 'yith-woocommerce-ajax-search')}
                        max={10}
                        min={1}
                        onBlur={function noRefCheck() {
                        }}
                        onChange={(value) => setAttributes({maxHistoryResults: value})}
                    />
                </PanelBody>
            </InspectorControls>
            <Disabled>
                { historyItems &&
                    (
                        <HistorySearches title={historyHeading} searches={historyItems} onClick={null}/>
                    )
                }
            </Disabled>
        </div>
    );
};

export default Edit;