import React from 'react';

import classnames from 'classnames';
import {
    InspectorControls,
    useBlockProps,
    __experimentalPanelColorGradientSettings as PanelColorGradientSettings,
} from '@wordpress/block-editor';
import {PanelBody, TextControl, RangeControl, CheckboxControl, Disabled} from '@wordpress/components';
import {__} from '@wordpress/i18n';
import {useStoreSearch} from '@ywcas/base-context/hooks';
import {RelatedContent} from '../../../../base/components';
import {useSearchContext} from '../../context';

const Edit = ({attributes, className, setAttributes, align}) => {
    const {query} = useSearchContext();
    const {
        relatedPostsHeading,
        maxPostsRelated,
        enabledPost,
        enabledPage,
        background,
    } = attributes;

    const {getResults} = useStoreSearch();
    const {isLoading: isSearching, results} = getResults(query);
    const blockProps = useBlockProps();

    const classes = classnames(
        'yith-related-posts-block',
        align ? 'align' + align : '',
        className,
    );

    return (
        <>
            <InspectorControls key="inspector">
                <PanelBody
                    title={__('Related posts settings', 'yith-woocommerce-ajax-search')}
                    initialOpen
                >
                    <TextControl
                        label={__('Label', 'yith-woocommerce-ajax-search')}
                        value={relatedPostsHeading}
                        placeholder={__(
                            'Enter the label for the related posts',
                            'yith-woocommerce-ajax-search',
                        )}
                        onChange={(value) =>
                            setAttributes({relatedPostsHeading: value})
                        }
                    />
                    <h3>{__('Show results related to', 'yith-woocommerce-ajax-search')}</h3>
                    <CheckboxControl
                        label={__('Post', 'yith-woocommerce-ajax-search')}
                        checked={enabledPost}
                        onChange={(value) => setAttributes({enabledPost: value})}
                    />
                    <CheckboxControl
                        label={__('Pages', 'yith-woocommerce-ajax-search')}
                        checked={enabledPage}
                        onChange={(value) => setAttributes({enabledPage: value})}
                    />
                    <RangeControl
                        help={__('Please, select the maximum number of related posts to show', 'yith-woocommerce-ajax-search')}
                        initialPosition={maxPostsRelated}
                        label={__('Max related posts', 'yith-woocommerce-ajax-search')}
                        max={10}
                        min={1}
                        onBlur={function noRefCheck() {
                        }}
                        onChange={(value) => setAttributes({maxPostsRelated: value})}
                    />
                    <h3>{__('Background color', 'yith-woocommerce-ajax-search')}</h3>
                    <div className="ywcas-block-panel-left-aligned-color-wrapper">
                        <PanelColorGradientSettings className="ywcas-related-posts-panel"
                                                    initialOpen={true}
                                                    settings={[
                                                        {
                                                            label: __('Background', 'yith-woocommerce-ajax-search'),
                                                            onColorChange: (color) => setAttributes({background: color}),
                                                            colorValue: background,
                                                        },
                                                    ]}
                        />
                    </div>
                </PanelBody>
            </InspectorControls>
            { (enabledPage || enabledPost) &&
            <div className={classes} {...blockProps}>
                <Disabled>
                    {!isSearching &&
                        <RelatedContent label={relatedPostsHeading} posts={results.related_content?.results}
                                        background={background} maxResults={maxPostsRelated}/>}
                </Disabled>
            </div>
            }

        </>
    );
};

export default Edit;