import React from 'react';
import {__} from '@wordpress/i18n';
import {Box} from '@lapilli-ui/components';
import classnames from 'classnames';
import {PanelBody, CheckboxControl, SelectControl, TextControl, ToggleControl, __experimentalNumberControl as NumberControl, Disabled} from '@wordpress/components';
import {InspectorControls, useBlockProps, __experimentalPanelColorGradientSettings as PanelColorGradientSettings} from '@wordpress/block-editor';

import {ProductResults} from '../../../../base/components';
import {useStoreSearch} from '@ywcas/base-context/hooks';
import {YWCAS_DEFAULT_QUERY} from '../../../../config';

const Edit = ({attributes, setAttributes}) => {
  const blockProps = useBlockProps();
  const {
    showName, showPrice, showImage, showCategories, showStock, showSKU, showSummary, limitSummary,
    showAddToCart, imageSize, imagePosition, productNameColor, priceLabel, maxResultsToShow, summaryMaxWord,
    noResults, showViewAll, showViewAllText, layout, showSaleBadge, showOutOfStockBadge, showFeaturedBadge, hideFeaturedIfOnSale
  } = attributes;

  const {results, isSearching, getCategories} = useStoreSearch({query: YWCAS_DEFAULT_QUERY, maxResults: maxResultsToShow, showCategories: showCategories});
  const categoryList = getCategories('all');
  const {isLoading, categories} = categoryList;
  const widthGrid = 'grid' === layout && showImage ? imageSize : 170;

  const classes = classnames(
      'ywcas-search-results',
      layout,
  );

  return (
      <>
        <InspectorControls key="inspector">
          <PanelBody
              title={__('Product results settings', 'yith-woocommerce-ajax-search')}
              initialOpen
          >
            <NumberControl
                label={__('Max results to show', 'yith-woocommerce-ajax-search')}
                value={(maxResultsToShow)}
                onChange={(value) =>
                    setAttributes({maxResultsToShow: (value)})
                }
            />
            <SelectControl
                label={__('Layout', 'yith-woocommerce-ajax-search')}
                value={layout}
                options={[
                  {label: __('Grid', 'yith-woocommerce-ajax-search'), value: 'grid'},
                  {label: __('List', 'yith-woocommerce-ajax-search'), value: 'list'},
                ]}
                onChange={(value) => setAttributes({layout: value})}
            />
            <h3>{__('Info to show', 'yith-woocommerce-ajax-search')}</h3>
            <ToggleControl
                label={__('Name', 'yith-woocommerce-ajax-search')}
                checked={showName}
                onChange={(value) => setAttributes({showName: value})}
            />
            {showName &&
                (<div className="ywcas-block-panel-left-aligned-color-wrapper">
                  <PanelColorGradientSettings className="ywcas-product-result-panel"
                                                                                                initialOpen={true}
                                                                                                settings={[
                                                                                                  {
                                                                                                    label: __('Product Title Color', 'yith-woocommerce-ajax-search'),
                                                                                                    onColorChange: (color) => setAttributes({productNameColor: color}),
                                                                                                    colorValue: productNameColor,
                                                                                                  },
                                                                                                ]}
            /></div>)

            }
            <ToggleControl
                label={__('Price', 'yith-woocommerce-ajax-search')}
                checked={showPrice}
                onChange={(value) => setAttributes({showPrice: value})}
            />
            {showPrice && <TextControl
                label={__('Price label', 'yith-woocommerce-ajax-search')}
                value={priceLabel}
                onChange={(value) =>
                    setAttributes({priceLabel: (value)})
                }
            />}

            <ToggleControl
                label={__('Image', 'yith-woocommerce-ajax-search')}
                checked={showImage}
                onChange={(value) => setAttributes({showImage: value})}
            />
            {showImage && 'list' === layout &&
                <SelectControl
                    label={__('Image position', 'yith-woocommerce-ajax-search')}
                    value={imagePosition}
                    options={[

                      {label: __('Show left', 'yith-woocommerce-ajax-search'), value: 'left'},
                      {label: __('Show right', 'yith-woocommerce-ajax-search'), value: 'right'},
                    ]}
                    onChange={(value) => setAttributes({imagePosition: value})}
                />
            }
            {showImage &&
                <NumberControl
                    label={__('Image size (px)', 'yith-woocommerce-ajax-search')}
                    isDragEnabled={true}
                    value={(imageSize)}
                    onChange={(value) =>
                        setAttributes({imageSize: (parseInt(value))})
                    }
                />
            }
            <ToggleControl
                label={__('Categories', 'yith-woocommerce-ajax-search')}
                checked={showCategories}
                onChange={(value) => setAttributes({showCategories: value})}
            />
            <ToggleControl
                label={__('Stock', 'yith-woocommerce-ajax-search')}
                checked={showStock}
                onChange={(value) => setAttributes({showStock: value})}
            />
            <ToggleControl
                label={__('SKU', 'yith-woocommerce-ajax-search')}
                checked={showSKU}
                onChange={(value) => setAttributes({showSKU: value})}
            />
            <ToggleControl
                label={__('Summary', 'yith-woocommerce-ajax-search')}
                checked={showSummary}
                onChange={(value) => setAttributes({showSummary: value})}
            />
            { showSummary && <ToggleControl
                        label={__('Limit summary length on/off', 'yith-woocommerce-ajax-search')}
                        checked={limitSummary}
                        onChange={(value) => setAttributes({limitSummary: value})}
                    />
            }
            { showSummary && limitSummary &&
              <NumberControl
                  label={__('Summary max words', 'yith-woocommerce-ajax-search')}
                  value={(summaryMaxWord)}
                  onChange={(value) =>
                      setAttributes({summaryMaxWord: (value)})
                  }
              />
            }
            <ToggleControl
                label={__('Add to cart', 'yith-woocommerce-ajax-search')}
                checked={showAddToCart}
                onChange={(value) => setAttributes({showAddToCart: value})}
            />

            <TextControl
                label={__('"No results" text', 'yith-woocommerce-ajax-search')}
                value={noResults}
                onChange={(value) =>
                    setAttributes({noResults: value})
                }
            />

            <ToggleControl
                label={__('Show "View all results" link', 'yith-woocommerce-ajax-search')}
                checked={showViewAll}
                onChange={(value) =>
                    setAttributes({showViewAll: value})
                }
            />

            {showViewAll && <TextControl
                label={__('"View all results" text', 'yith-woocommerce-ajax-search')}
                value={showViewAllText}
                onChange={(value) =>
                    setAttributes({showViewAllText: value})
                }
            />}

            <ToggleControl
                label={__('Show "On Sale" badge', 'yith-woocommerce-ajax-search')}
                checked={showSaleBadge}
                onChange={(value) => setAttributes({showSaleBadge: value})}
            />

            <ToggleControl
                label={__('Show "Out of stock" badge', 'yith-woocommerce-ajax-search')}
                checked={showOutOfStockBadge}
                onChange={(value) => setAttributes({showOutOfStockBadge: value})}
            />

            <ToggleControl
                label={__('Show "Featured" badge', 'yith-woocommerce-ajax-search')}
                checked={showFeaturedBadge}
                onChange={(value) => setAttributes({showFeaturedBadge: value})}
            />

            <ToggleControl
                label={__('Hide "Featured" badge if the product is on sale', 'yith-woocommerce-ajax-search')}
                checked={hideFeaturedIfOnSale}
                onChange={(value) => setAttributes({hideFeaturedIfOnSale: value})}
            />
          </PanelBody>
        </InspectorControls>

        <Box className={classes} {...blockProps}>
          {layout === 'grid' && <style>{`
                .wp-block-yith-product-results-block .lapilliUI-Grid__root{
                     grid-template-columns: repeat(auto-fill, minmax(${widthGrid}px, 1fr))!important;
                     row-gap:30px;
                     column-gap:15px
                     }
                `}
          </style>}
          <Disabled>
            {!isSearching && !isLoading && categories.length && <ProductResults searchQuery={YWCAS_DEFAULT_QUERY} results={results} onClick={null} categoryList={categories} {...attributes} />}
          </Disabled>
        </Box>

      </>
  )
      ;
};

export default Edit;