import {registerBlockType} from '@wordpress/blocks';
import {__} from '@wordpress/i18n';

import {yith_icon} from '../../common';
import Edit from './edit';
import Save from './save';
import {blockName, blockAttributes} from './attributes';

const blockConfig = {
  title: __('Classic Search', 'yith-woocommerce-ajax-search'),
  description: __('Add the classic search block', 'yith-woocommerce-ajax-search'),
  icon: yith_icon,
  apiVersion: 2,
  category: 'yith-blocks',
  keywords: ['search','yith','ajax'],
  supports: {
    align: ['wide'],
    html: true,
    multiple: true,
    inserter:true
  },
  edit: Edit,
  save: Save,
  attributes: blockAttributes,
};

registerBlockType(blockName, blockConfig);
