import React from 'react';
import classnames from 'classnames';
import {PanelBody, SelectControl} from '@wordpress/components';
import {InnerBlocks, InspectorControls, useBlockProps} from '@wordpress/block-editor';
import { withInstanceId } from '@wordpress/compose';
import './inner-blocks';
import {EditorProvider} from "../../base/providers/editor-context";
import {Root} from "../../base/components";
import {__} from "@wordpress/i18n";
import {getSizeClassName} from "../../common";


// Array of allowed block names.
const ALLOWED_BLOCKS = [
    'yith/empty-block',
    'yith/fill-block',
    'yith/input-block',
];

const Edit = ({attributes,setAttributes}) => {
    const blockProps = useBlockProps();
    const {currentView, isPreview = false, size} = attributes;
    const defaultTemplate = [
        ['yith/input-block', {}, []],
        ['yith/filled-block', {}, []],
        ['yith/empty-block', {}, []],
    ];
    const classes = classnames(
        'ywcas-classic-search',
        getSizeClassName(size),
    );
    blockProps.className = blockProps.className+' '+classes;
    return (
        <div {...blockProps} style={{marginLeft: '20px', marginRight: '20px'}}>
            <InspectorControls key="inspector">
                <PanelBody
                    title={__('Settings', 'yith-woocommerce-ajax-search')}
                    initialOpen
                >
                    <SelectControl
                        label={__('Size', 'yith-woocommerce-ajax-search')}
                        value={size}
                        options={[

                            {label: __('Small', 'yith-woocommerce-ajax-search'), value: 'sm'},
                            {label: __('Large', 'yith-woocommerce-ajax-search'), value: 'lg'},
                        ]}
                        onChange={(value) => setAttributes({size: value})}
                    />
                </PanelBody>
            </InspectorControls>
            <EditorProvider
                currentView={currentView}
                isPreview={isPreview}
                size={size}
                previewData={[]}
            >
                <Root>
                    <InnerBlocks
                        allowedBlocks={ALLOWED_BLOCKS}
                        template={defaultTemplate}
                        templateLock="insert"
                    />
                </Root>
            </EditorProvider>
        </div>
    );
};

export default withInstanceId( Edit );
