import React from 'react';

import classnames from 'classnames';
import {InspectorControls, useBlockProps} from '@wordpress/block-editor';
import {PanelBody, TextControl, RangeControl, Disabled} from '@wordpress/components';
import {__} from '@wordpress/i18n';
import {CategoryResults} from '../../../../base/components';
import {useStoreSearch} from '@ywcas/base-context/hooks';
import {YWCAS_DEFAULT_QUERY} from "../../../../config";
import {useSearchContext} from "../../context";


const Edit = ({attributes, className, setAttributes, align}) => {

    const {query} = useSearchContext();
    const {getResults, getCategories} = useStoreSearch();
    const {
        relatedCategoryHeading,
        maxCategoryRelated
    } = attributes;

    const blockProps = useBlockProps();
    const categories = getCategories('all');
    const {isLoading: isSearching, results} = getResults(query);
    const classes = classnames(
        'yith-releated-category-block',
        align ? 'align' + align : '',
        className,
    );

    return (
        <>
            <InspectorControls key="inspector">
                <PanelBody
                    title={__('Related categories settings', 'yith-woocommerce-ajax-search')}
                    initialOpen
                >
                    <TextControl
                        label={__('Label', 'yith-woocommerce-ajax-search')}
                        value={relatedCategoryHeading}
                        placeholder={__(
                            'Enter the label for the related categories',
                            'yith-woocommerce-ajax-search',
                        )}
                        onChange={(value) =>
                            setAttributes({relatedCategoryHeading: value})
                        }
                    />
                    <RangeControl
                        help={__('Please, select the maximum number of related categories to show', 'yith-woocommerce-ajax-search')}
                        initialPosition={maxCategoryRelated}
                        label={__('Max related categories', 'yith-woocommerce-ajax-search')}
                        max={10}
                        min={1}
                        onBlur={function noRefCheck() {
                        }}
                        onChange={(value) => setAttributes({maxCategoryRelated: value})}
                    />
                </PanelBody>
            </InspectorControls>

            <div className={classes} {...blockProps}>
                <Disabled>
                    {!isSearching && !categories.isLoading && categories.categories.length &&
                        <CategoryResults label={relatedCategoryHeading} query={YWCAS_DEFAULT_QUERY}
                                         categories={categories.categories} results={results.results}
                                         maxResults={maxCategoryRelated}/>}
                </Disabled>
            </div>

        </>
    );
};

export default Edit;