import {InnerBlocks} from '@wordpress/block-editor';
import {useBlockProps} from '@wordpress/block-editor';
import {useEditorContext} from '../../../../base/providers/editor-context';
import React from 'react';
import {useForcedLayout} from "../../../../shared";

const Edit = ({clientId}) => {
    const blockProps = useBlockProps();
    const {currentView} = useEditorContext();

    const defaultTemplate = [
        ['yith/history-block', {}, []],
        ['yith/popular-block', {}, []],
    ];

    const allowedBlocks = [
        'yith/history-block',
        'yith/popular-block',
        'core/column',
        'core/columns',
        'core/heading',
        'core/paragraph',
        'core/image',
        'core/buttons',
        'core/group',
        'core/spacer'
    ];

    useForcedLayout({
        clientId: clientId,
        registeredBlocks: allowedBlocks,
        defaultTemplate
    });
    return (
        <div {...blockProps}
             hidden={currentView !== 'yith/empty-block'}>
            <div className='wp-block-yith-popover-empty-block'>
                <InnerBlocks
                    allowedBlocks={ allowedBlocks }
                    template={defaultTemplate}
                    templateLock={false}
                    renderAppender={InnerBlocks.ButtonBlockAppender}
                />
            </div>
        </div>
    );
};

export default Edit;
