import React from 'react';
import {__} from '@wordpress/i18n';
import {InspectorControls, useBlockProps} from '@wordpress/block-editor';
import {PanelBody, RangeControl, TextControl, Disabled} from '@wordpress/components';

import {PopularSearches} from "../../../../base/components";
import {popular} from '../../../../config';
const Edit = ({ attributes, setAttributes, className, align }) => {
  const {popularHeading, maxPopularResults} = attributes;
  const blockProps = useBlockProps();
  const popularItems = popular.slice(0, maxPopularResults);

  return (
      <div {...blockProps}>
        <InspectorControls key="inspector">
          <PanelBody
              title={ __( 'Settings', 'yith-woocommerce-ajax-search' ) }
              initialOpen
          >
            <TextControl
                label={__( 'Label', 'yith-woocommerce-ajax-search' )}
                className="wc-block-product-search__field input-control"
                value={ popularHeading }
                placeholder={ __(
                    'Enter the label for the search history',
                    'yith-woocommerce-ajax-search'
                ) }
                onChange={ ( value ) =>
                    setAttributes({popularHeading: value} )
                }
            />
            <RangeControl
                help={__('Please, select the maximum number of popular searches to show.', 'yith-woocommerce-ajax-search')}
                initialPosition={maxPopularResults}
                label={__('Searches to show', 'yith-woocommerce-ajax-search')}
                max={10}
                min={1}
                onBlur={function noRefCheck() {
                }}
                onChange={(value) => setAttributes({maxPopularResults: value})}
            />
          </PanelBody>
        </InspectorControls>
        <Disabled>
        {popularItems &&
            <PopularSearches title={popularHeading} searches={popularItems}/>
        }
        </Disabled>
      </div>
  );
};

export default Edit;