import {InnerBlocks, useBlockProps} from '@wordpress/block-editor';
import {useEditorContext} from '../../../../base/providers/editor-context';
import React from 'react';

import {useForcedLayout} from "../../../../shared";

const Edit = ({clientId}) => {
    const blockProps = useBlockProps();
    const {currentView} = useEditorContext();

    const defaultTemplate = [
        ['yith/related-categories-block', {}, []],
        ['core/separator', {
            align: 'wide', className: "is-style-wide ywcas-separator",
            style: {
                color: {
                    background: '#9797972e'
                },
                spacing: {
                    margin: {
                        top: '10px',
                        bottom: '10px'
                    }
                }
            }
        }, []],
        ['yith/product-results-block', {}, []],
        ['yith/related-posts-block',{},[]]
    ];
    const allowedBlocks = [
        'yith/history-block',
        'yith/popular-block',
        'yith/product-results-block',
        'yith/related-categories-block',
        'yith/related-posts-block',
        'core/column',
        'core/columns',
        'core/heading',
        'core/paragraph',
        'core/image',
        'core/buttons',
        'core/group',
        'core/separator',
        'core/spacer'
    ];
    useForcedLayout({
        clientId: clientId,
        registeredBlocks: allowedBlocks,
        defaultTemplate
    });
    return (
        <div {...blockProps}
             hidden={currentView !== 'yith/filled-block'}>
            <div className='wp-block-yith-popover-filled-block'>
            <InnerBlocks
                allowedBlocks ={allowedBlocks}
                template={defaultTemplate}
                templateLock={false}
                renderAppender={ InnerBlocks.ButtonBlockAppender }
            />
            </div>
        </div>
    );
};

export default Edit;